import React, { useEffect, useMemo, useState } from "react";
import type { BannerAd } from "@shared/types";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
  type CarouselApi,
} from "./ui/carousel";

/* ---------- URL helpers ---------- */

// 1) API / Assets base (set any one in .env if needed)
const ASSETS_BASE =
  (import.meta as any)?.env?.VITE_ASSETS_BASE_URL ||
  (import.meta as any)?.env?.VITE_API_BASE_URL ||
  "";

/** Clean slashes like: '//' '\/' etc */
const trimSlashes = (s: string) => s.replace(/\/+$/g, "");

/** Make absolute URL from possibly relative path */
const toAbsolute = (u?: string | null): string | null => {
  if (!u || typeof u !== "string") return null;
  let x = u.trim().replace(/\\/g, "/"); // windows backslash  forward slash
  if (!x) return null;

  // Fully qualified
  if (/^https?:\/\//i.test(x)) return x;

  // Protocol-relative
  if (x.startsWith("//")) return window.location.protocol + x;

  // Leading slash  join with BASE or origin
  const base = ASSETS_BASE ? trimSlashes(ASSETS_BASE) : window.location.origin;
  if (x.startsWith("/")) return `${base}${x}`;

  // Relative like 'uploads/a.jpg'  base + '/' + path
  return `${base}/${x.replace(/^\.?\//, "")}`;
};

/** API may send any of these keys */
const readImgKey = (b: any): string | null =>
  b?.imageUrl ?? b?.image ?? b?.url ?? null;

/* ---------- Fallbacks ---------- */
const FALLBACKS = [
  "https://images.unsplash.com/photo-1502672260266-1c1ef2d93688?q=80&w=1600&auto=format&fit=crop",
  "https://images.unsplash.com/photo-1505693416388-ac5ce068fe85?q=80&w=1600&auto=format&fit=crop",
  "https://images.unsplash.com/photo-1560185127-6ed189bf02f4?q=80&w=1600&auto=format&fit=crop",
];

/* ---------- Component ---------- */
const HeroImageSlider: React.FC = () => {
  const [banners, setBanners] = useState<BannerAd[]>([]);
  const [api, setApi] = useState<CarouselApi>();
  const [current, setCurrent] = useState(0);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const defaultSlides = useMemo<BannerAd[]>(
    () =>
      FALLBACKS.map((src, i) => ({
        _id: `fb-${i + 1}`,
        title:
          i === 0
            ? "Premium Properties in Rohtak"
            : i === 1
            ? "Find Your Perfect Home"
            : "Buy  Sell  Rent",
        imageUrl: src,
        link: "",
        sortOrder: i + 1,
      })),
    []
  );

  useEffect(() => {
    let mounted = true;

    const load = async () => {
      try {
        setLoading(true);
        setError(null);

        const ctl = new AbortController();
        const t = setTimeout(() => ctl.abort(), 8000);

        const res = await fetch("/api/banners?active=true", {
          signal: ctl.signal,
          headers: { "Content-Type": "application/json" },
        });
        clearTimeout(t);

        let data: any = null;
        try {
          data = await res.json();
        } catch {
          data = null;
        }

        const incoming: any[] =
          (Array.isArray(data?.data) && data.data) ||
          (Array.isArray(data?.banners) && data.banners) ||
          (Array.isArray(data) && data) ||
          [];

        const cleaned = incoming
          .map((x) => {
            const raw = readImgKey(x);
            const abs = toAbsolute(raw);
            // debug so tum console me dekh sako exactly kya URL ban raha
            console.debug("BANNER_URL_RESOLVE:", { raw, abs, ASSETS_BASE });
            return {
              ...x,
              imageUrl: abs,
              sortOrder: typeof x?.sortOrder === "number" ? x.sortOrder : 999,
            };
          })
          .filter((x) => !!x.imageUrl)
          .sort((a, b) => a.sortOrder - b.sortOrder);

        if (!mounted) return;
        setBanners(cleaned.length ? cleaned : defaultSlides);
      } catch (e: any) {
        if (!mounted) return;
        console.error("Banner load error:", e?.message || e);
        setError("Unable to load banners");
        setBanners(defaultSlides);
      } finally {
        mounted && setLoading(false);
      }
    };

    load();

    const refetch = () => load();
    window.addEventListener("bannerUpdate", refetch);
    window.addEventListener("bannerRefresh", refetch);
    return () => {
      mounted = false;
      window.removeEventListener("bannerUpdate", refetch);
      window.removeEventListener("bannerRefresh", refetch);
    };
  }, [defaultSlides]);

  useEffect(() => {
    if (!api) return;
    const onSelect = () => setCurrent(api.selectedScrollSnap());
    api.on("select", onSelect);
    return () => {
      api.off("select", onSelect as any);
    };
  }, [api]);

  useEffect(() => {
    if (!api || banners.length <= 1) return;
    let paused = false;
    const el = document.querySelector(".hero-carousel");
    const onEnter = () => (paused = true);
    const onLeave = () => (paused = false);

    el?.addEventListener("mouseenter", onEnter);
    el?.addEventListener("mouseleave", onLeave);

    const id = setInterval(() => {
      if (!paused) {
        if (api.canScrollNext()) api.scrollNext();
        else api.scrollTo(0);
      }
    }, 5000);

    return () => {
      clearInterval(id);
      el?.removeEventListener("mouseenter", onEnter);
      el?.removeEventListener("mouseleave", onLeave);
    };
  }, [api, banners.length]);

  const clickBanner = (b: BannerAd) => {
    if (!b?.link) return;
    if (b.link.startsWith("http")) window.open(b.link, "_blank", "noopener,noreferrer");
    else window.location.href = b.link;
  };

  const go = (i: number) => api?.scrollTo(i);

  if (loading) {
    return (
      <div className="hero-carousel relative w-full h-[72vh] sm:h-[64vh] md:h-[56vh] lg:h-[60vh] min-h-[340px] bg-gray-200 animate-pulse" />
    );
  }

  return (
    <div className="hero-carousel relative w-full h-[72vh] sm:h-[64vh] md:h-[56vh] lg:h-[60vh] min-h-[340px] overflow-hidden bg-black">
      <Carousel opts={{ align: "start", loop: true }} setApi={setApi} className="absolute inset-0">
        <CarouselContent className="h-full -ml-0">
          {banners.map((b, i) => (
            <CarouselItem key={b._id || i} className="h-full basis-full pl-0">
              <div
                className={`relative w-full h-full ${b?.link ? "cursor-pointer" : ""}`}
                onClick={() => clickBanner(b)}
              >
                {/*  Absolute <img>: paint safe, shows errors, covers full area */}
                <img
                  src={b.imageUrl!}
                  alt={b.title || `banner-${i + 1}`}
                  className="absolute inset-0 w-full h-full object-cover"
                  loading={i === 0 ? "eager" : "lazy"}
                  onError={(e) => {
                    console.warn("Banner image failed, using fallback", b.imageUrl);
                    (e.target as HTMLImageElement).src = FALLBACKS[i % FALLBACKS.length];
                  }}
                />
                {/* overlay */}
                <div className="absolute inset-0 bg-black/45" />
                {/* centered copy */}
                <div className="absolute inset-0 z-10 flex items-center justify-center px-4">
                  <div className="text-center text-white max-w-4xl mx-auto">
                    <h1 className="text-3xl sm:text-4xl md:text-5xl font-bold mb-2 drop-shadow">
                      {b.title || "Premium Properties in Rohtak"}
                    </h1>
                    <p className="text-sm sm:text-base md:text-lg text-white/90 mb-4 max-w-2xl mx-auto">
                      {(b as any)?.description ||
                        "Discover amazing properties with our trusted platform"}
                    </p>
                    {b?.link && (
                      <button
                        onClick={(e) => {
                          e.stopPropagation();
                          clickBanner(b);
                        }}
                        className="bg-[#C70000] hover:bg-[#A60000] text-white px-6 py-3 rounded-lg font-semibold shadow-lg"
                      >
                        Learn More
                      </button>
                    )}
                  </div>
                </div>
              </div>
            </CarouselItem>
          ))}
        </CarouselContent>

        {banners.length > 1 && (
          <>
            <CarouselPrevious className="absolute left-3 top-1/2 -translate-y-1/2 bg-white/25 hover:bg-white/35 text-white border-none backdrop-blur z-10" />
            <CarouselNext className="absolute right-3 top-1/2 -translate-y-1/2 bg-white/25 hover:bg-white/35 text-white border-none backdrop-blur z-10" />
          </>
        )}
      </Carousel>

      {banners.length > 1 && (
        <div className="absolute bottom-6 left-1/2 -translate-x-1/2 flex gap-2 z-20">
          {banners.map((_, i) => (
            <button
              key={i}
              onClick={() => go(i)}
              className={`w-2.5 h-2.5 rounded-full transition-all ${
                i === current ? "bg-white scale-110" : "bg-white/60 hover:bg-white/80"
              }`}
              aria-label={`Go to slide ${i + 1}`}
            />
          ))}
        </div>
      )}

      {error && (
        <div className="absolute top-3 right-3 z-30 text-xs bg-red-600/90 text-white px-2.5 py-1.5 rounded">
          {error}
        </div>
      )}
    </div>
  );
};

export default HeroImageSlider;
